import { useEffect, useState } from "react";
import { PageInfo, PatternInfo } from "../utils/types";
import {
  useParams,
  Outlet,
  useOutletContext,
  useSearchParams,
} from "react-router-dom";
import { Box, Divider, Flex } from "@chakra-ui/react";
import PageMenu from "../components/PageMenu";
import SelectSize from "../components/SelectSize";

type ContextType = {
  menu: number;
  size: number;
};

export default function PatternBySize() {
  const [patternInfo, setPatternInfo] = useState<PatternInfo | null>(null);
  const [selectedSize, setSelectedSize] = useState<number>(0);
  const [selectedMenu, setSelectedMenu] = useState<number>(0);

  const params = useParams();
  const [searchParams, setSearchParams] = useSearchParams();

  // Load project info
  useEffect(() => {
    (async () => {
      // Store pattern into to context
      const info = await import(`../projects/${params.projectID}/pattern`);
      setPatternInfo(info.patternInfo);

      // Size from URL
      // since + isn't recognized by searchParams, use location
      let size = searchParams.get("size");
      if (size?.charAt(size.length - 1) === " ") {
        size = `${size?.slice(0, size.length - 1)}+`;
      }
      if (size == null) {
        // If size isn't set in URL, set the first size in the URL
        setSearchParams({ size: info.patternInfo.sizes[0] });
      } else {
        // If size exists in the URL, update the Select value
        setSelectedSize(Math.max(info.patternInfo.sizes.indexOf(size), 0));
      }

      // Menu from URL
      const part = params.part ?? "overview";
      setSelectedMenu(
        // Update part menu
        info.patternInfo.pageInfo.map((p: PageInfo) => p.id).indexOf(part)
      );
    })();
  }, [params.part, params.projectID, searchParams, setSearchParams]);

  if (patternInfo == null) {
    return null;
  }

  const { sizes, pageInfo } = patternInfo;

  const onChange = (id: number) => {
    // const id = +e.target.value;
    setSelectedSize(id);
    setSearchParams({ size: patternInfo.sizes[id] });
  };
  return (
    <Box
      style={{ background: "white" }}
      p={4}
      ml={2}
      mr={2}
      color="var(--oc-gray-8)"
      borderRadius={8}
    >
      <Flex gap={6} flexWrap="wrap" align="center">
        <SelectSize
          sizes={sizes}
          selectedSize={selectedSize}
          onChange={onChange}
        />
        <PageMenu
          pageInfo={pageInfo}
          selectedMenu={selectedMenu}
          setSelectedMenu={setSelectedMenu}
          searchQueries={[
            { term: "size", value: patternInfo.sizes[selectedSize] },
          ]}
        />
      </Flex>
      <Divider pt={4} mb={4} variant="dashed" />
      <Outlet context={{ menu: selectedMenu, size: selectedSize }} />
    </Box>
  );
}

export function usePatternBySize() {
  return useOutletContext<ContextType>();
}
